import infectiousDiseasesProvider from "@data-access/infectious-diseases-provider";
import snackbar from "@utils/snackbar-utils";
import stringUtils from "mainam-react-native-string-utils";
import moment from "moment";
import { Modal } from "antd";
const { confirm } = Modal;

function updateData(data) {
  return dispatch => {
    dispatch({
      type: "INFECTIOUS-DISEASES-UPDATE-DATA",
      data: data
    });
  };
}

function onSizeChange(size) {
  return (dispatch, getState) => {
    dispatch(
      updateData({
        size: size
      })
    );
    dispatch(gotoPage(0));
  };
}

function onSearch(data, action) {
  return (dispatch, getState) => {
    let searchValue = action === "value" ? data : getState().infectiousDiseases.searchValue;
    let searchName = action === "name" ? data : getState().infectiousDiseases.searchName;
    if (searchName === undefined && searchValue === undefined) {
    } else {
      dispatch(
        updateData({
          searchName: searchName,
          searchValue: searchValue,
        })
      );
    }
    dispatch(gotoPage(0));
  };
}

function gotoPage(page) {
  return (dispatch, getState) => {
    dispatch(updateData({ page: page }));
    let size = getState().infectiousDiseases.size || 10;
    let name = getState().infectiousDiseases.searchName;
    let value = getState().infectiousDiseases.searchValue;
    infectiousDiseasesProvider.search(page, size, name, value, undefined).then(s => {
      if (s && s.code === 0) {
        dispatch(
          updateData({
            total: s.totalElements || size,
            data: s.data || []
          })
        );
      }
    });
  };
}

function loadDetail(id) {
  return (dispatch, getState) => {
    return new Promise((resolve, reject) => {
      infectiousDiseasesProvider
        .getById(id)
        .then(s => {
          if (s && s.code == 0 && s.data) {
            dispatch(
              updateData({
                id: s.data.id,
                name: s.data.name,
                value: s.data.value,
                description: s.data.description
              })
            );
            resolve(s.data);
            return;
          }
          snackbar.show("Khng tm thy kt qu ph hp", "danger");
          reject(s);
        })
        .catch(e => {
          snackbar.show(
            e && e.message ? e.message : "Xy ra li, vui lng th li sau",
            "danger"
          );
          reject(e);
        });
    });
  };
}

function loadList() {
  return (dispatch, getState) => {
    infectiousDiseasesProvider.search(0, 1000, "", "").then(s => {
      switch (s.code) {
        case 0:
          dispatch(
            updateData({
              infectiousDiseases: s.data,
              total: s.totalElements,
            })
          );
          break;
      }
    });
  };
}

function createOrEdit() {
  return (dispatch, getState) => {
    return new Promise((resolve, reject) => {
      let id = getState().infectiousDiseases.id;
      let active = true;
      let name = getState().infectiousDiseases.name;
      let value = getState().infectiousDiseases.value;
      let lines = getState().infectiousDiseases.dataIndex
      infectiousDiseasesProvider
        .createOrEdit(id, active, name, value, lines)
        .then(s => {
          if (s.code == 0) {
            dispatch(
              updateData({
                id: "",
                name: "",
                value: "",
                lines: [],
                dataIndex: [],
                active: false,
                isOpen: false
              })
            );
            if (!id) {
              snackbar.show("Thm mi d liu thnh cng", "success");
            } else {
              snackbar.show("Cp nht d liu thnh cng", "success");
            }
            dispatch(gotoPage(0));
            // dispatch(loadList());
            resolve(s.data);
          } else {
            if (!id) {
              snackbar.show(s.message || "Thm mi d liu thnh cng", "danger");
            } else {
              snackbar.show(s.message || "Cp nht d liu thnh cng", "danger");
            }
            reject();
          }
        })
        .catch(e => {
          snackbar.show("Thm gi d liu khng thnh cng", "danger");
          reject();
        });
    });
  };
}

function onDeleteItem(item) {
  return (dispatch, getState) => {
    return new Promise((resolve, reject) => {
      confirm({
        title: "Xc nhn",
        content: `Bn c mun xa nhm bnh  ${item.name}?`,
        okText: "Xa",
        okType: "danger",
        cancelText: "Hy",
        onOk() {
          infectiousDiseasesProvider
            .delete(item.id)
            .then(s => {
              if (s.code == 0) {
                snackbar.show("Xa d liu thnh cng", "success");
                let data = getState().infectiousDiseases.data || [];
                let index = data.findIndex(x => x.id == item.id);
                if (index != -1);
                data.splice(index, 1);
                dispatch(
                  updateData({
                    data: [...data]
                  })
                );
                dispatch(gotoPage(0));
                resolve();
              } else if (s.code == 605) {
                snackbar.show("Nhm bnh truyn nhim  c s dng, vui lng kim tra li!", "danger");
                reject();
              } else {
                snackbar.show("Xa d liu khng thnh cng", "danger");
                reject();
              }
            })
            .catch(e => {
              snackbar.show("Xa d liu khng thnh cng", "danger");
              reject();
            });
        },
        onCancel() {
          reject();
        }
      });
    });
  };
}

export default {
  loadList,
  createOrEdit,
  updateData,
  gotoPage,
  onSearch,
  onSizeChange,
  onDeleteItem,
  loadDetail,
};
