import departmentsProvider from "@data-access/departments-provider";
import snackbar from "@utils/snackbar-utils";
import stringUtils from "mainam-react-native-string-utils";
import moment from "moment";
import { Modal } from "antd";
const { confirm } = Modal;

function updateData(data) {
  return dispatch => {
    dispatch({
      type: "DEPARTMENTS-UPDATE-DATA",
      data: data
    });
  };
}

function onSizeChange(size) {
  return (dispatch, getState) => {
    dispatch(
      updateData({
        size: size
      })
    );
    dispatch(gotoPage(0));
  };
}

function onSearch(item, action) {
  return (dispatch, getState) => {
    let searchValue = action === "value" ? item : getState().departments.searchValue
    let searchName = action === 'name' ? item : getState().departments.searchName
    if (searchValue === undefined && searchName === undefined) {
    } else {
      dispatch(
        updateData({
          searchName: searchName,
          searchValue: searchValue,
        })
      );
    }
    dispatch(gotoPage(0));
  };
}

function gotoPage(page) {
  return (dispatch, getState) => {
    dispatch(updateData({ page: page }));
    let size = getState().departments.size || 10;
    let name = getState().departments.searchName;
    let value = getState().departments.searchValue;
    let sort = getState().departments.sort || {};
    departmentsProvider.search(page, size, name, value).then(s => {
      if (s && s.code === 0) {
        dispatch(
          updateData({
            total: s.totalElements || size,
            data: s.data || []
          })
        );
      }
    });
  };
}

function loadDetail(id) {
  return (dispatch, getState) => {
    return new Promise((resolve, reject) => {
      departmentsProvider
        .getById(id)
        .then(s => {
          if (s && s.code == 0 && s.data) {
            dispatch(
              updateData({
                id: s.data.id,
                name: s.data.name,
                value: s.data.value,
              })
            );
            resolve(s.data);
            return;
          }
          snackbar.show("Khng tm thy kt qu ph hp", "danger");
          reject(s);
        })
        .catch(e => {
          snackbar.show(
            e && e.message ? e.message : "Xy ra li, vui lng th li sau",
            "danger"
          );
          reject(e);
        });
    });
  };
}

function loadList() {
  return (dispatch, getState) => {
    departmentsProvider.search(0, 1000, "", "").then(s => {
      switch (s.code) {
        case 0:
          dispatch(
            updateData({
              departments: s.data,
              total: s.totalElements,
            })
          );
          break;
      }
    });
  };
}

function createOrEdit() {
  return (dispatch, getState) => {
    return new Promise((resolve, reject) => {
      let id = getState().departments.id;
      let active = true;
      let name = getState().departments.name;
      let value = getState().departments.value;
      departmentsProvider
        .createOrEdit(id, active, name, value)
        .then(s => {
          if (s.code == 0) {
            dispatch(
              updateData({
                id: "",
                name: "",
                value: "",
                dataIndex: [],
                active: false,
                isOpen: false
              })
            );
            if (!id) {
              snackbar.show("Thm khoa thnh cng", "success");
            } else {
              snackbar.show("Cp nht khoa thnh cng", "success");
            }
            dispatch(loadList());
            resolve(s.data);
          } else {
            if (!id) {
              snackbar.show(s.message || "Thm khoa khng thnh cng", "danger");
            } else {
              snackbar.show(s.message || "Sa khoa khng thnh cng", "danger");
            }
            reject();
          }
        })
        .catch(e => {
          snackbar.show("Thm khoa khng thnh cng", "danger");
          reject();
        });
    });
  };
}

function onDeleteItem(item) {
  return (dispatch, getState) => {
    return new Promise((resolve, reject) => {
      confirm({
        title: "Xc nhn",
        content: `Bn c mun xa khoa ${item.name}?`,
        okText: "Xa",
        okType: "danger",
        cancelText: "Hy",
        onOk() {
          departmentsProvider
            .delete(item.id)
            .then(s => {
              if (s.code == 0) {
                snackbar.show("Xa khoa thnh cng", "success");
                let data = getState().departments.data || [];
                let index = data.findIndex(x => x.id == item.id);
                if (index != -1);
                data.splice(index, 1);
                dispatch(
                  updateData({
                    data: [...data]
                  })
                );
                dispatch(gotoPage(0));
                resolve();
              } else {
                snackbar.show("Xa khoa khng thnh cng", "danger");
                reject();
              }
            })
            .catch(e => {
              snackbar.show("Xa khoa khng thnh cng", "danger");
              reject();
            });
        },
        onCancel() {
          reject();
        }
      });
    });
  };
}
function reset (){
  return (dispatch) => {
  departmentsProvider.syncs().then(s => {
    if (s && s.code === 0) {
      snackbar.show("ng b d liu thnh cng!", "success");
      dispatch(onSearch("", "", -1));
      dispatch(loadList());
    } else {
      snackbar.show("ng b d liu tht bi!", "danger");
    }
  }).catch(e => {

      })
  }
}
export default {
  loadList,
  createOrEdit,
  updateData,
  gotoPage,
  onSearch,
  onSizeChange,
  onDeleteItem,
  loadDetail,
  reset,
};
